.pagination{
    text-align: center;
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 18px;
    overflow-x: auto;
}
.pagination span{
    margin: 0 5px;
    cursor: pointer;
}
.checked{
    font-weight: bold;
}
.prev, .next{
    border-radius: 5px;
    border: 1px solid black;
    background: white;
    color: blue;
    padding: 5px 10px;
}